import * as d3 from 'd3';
import * as axios from 'axios';
import * as Sortable from 'sortablejs';
import NetworkChip from './NetworkChip.vue';

// export interface QueuedModel {
//     layersData: any[];
//     params: number;
//     estimated_training: number;
//     recency: number;
//     isTraining: false;
